<template>
    <el-container>
        <el-aside style="padding: 20px">
            <el-card
                    style="display: flex;flex-direction: column;justify-content: center;text-align: center;box-shadow: none;">
                <div>广告位</div>
                <div>广告位</div>
                <div>广告位</div>
            </el-card>
        </el-aside>
        <el-main>
            <el-card>
                <el-row :gutter="10">
                    <el-col :span="24" style="margin-top: 10px">
                        <el-input type="text" v-model="api.title" placeholder="请在此处输入标题"></el-input>
                    </el-col>
                    <el-col :span="24" style="margin-top: 10px">
                        <el-select v-model="api.category" placeholder="请选择发布到的圈子">
                            <el-option v-for="item in option" :key="item.id" :label="item.group_name" :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="24">
                        <div class="publish" style="margin-top: 10px">
                            <quill-editor ref="text" v-model="content" class="myQuillEditor"
                                          :options="editorOption"
                                          style="margin-bottom: 10px;min-height: 200px;"></quill-editor>
                        </div>
                        <el-button type="primary" @click="publish">提交</el-button>
                    </el-col>
                </el-row>
            </el-card>

            <el-dialog :visible.sync="dialogVisible">
                <el-form>

                </el-form>
                <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
              </span>
            </el-dialog>
        </el-main>
        <el-aside style="padding: 20px">
            <el-card
                    style="display: flex;flex-direction: column;justify-content: center;text-align: center;box-shadow: none;">
                <div>广告位</div>
                <div>广告位</div>
                <div>广告位</div>
            </el-card>
        </el-aside>
    </el-container>
</template>

<script>
    import quillEditor from 'vue-quill-editor'
    import axios from "axios"
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import '../../static/overwrite.css'

    export default {
        name: "publish",
        data() {
            return {
                category: {},
                content: '',
                editorOption: {},
                dialogVisible: false,
                option: [],
                api: {
                    category: ""
                    , title: ""
                }
            }
        },
        components: quillEditor,
        mounted() {
            this.$axios.request({
                url: "/index/column/show"
            }).then((response) => {
                this.option = response.data.data;
            })
        },
        methods: {
            submit() {
                console.log(this.$refs.text.value)
            }
            , handleClose(done) {
                this.dialogVisible = false;
            },
            publish() {
                // this.dialogVisible = true;
                axios.request({
                    url: "/bbs/publish"
                    , data: {
                        category: this.api.category,
                        title: this.api.title,
                        content: this.content
                    },
                    method: "post"
                }).then((response) => {
                    alert(response.status)
                })
            }
        },
    }
</script>

<style scoped>

</style>



