<template>
    <el-container>
        <el-main style="min-height: 50rem">
            <el-row :gutter="10">
                <el-col :span="24">
                    <el-card class="box-card">
                        <p id="title">
                            <!--                            标题框-->
                            {{title}}
                        </p>
                        <el-divider></el-divider>
                        <div id="content">
                            <!--内容框-->
                            <el-card>
                                <div v-html="content"></div>
                            </el-card>
                        </div>
                        <el-divider>评论</el-divider>
                        <div id="comment">
                            <!--                            评论区域-->
                            <div v-for="(item,key) in commentData" :key="key">
                                <div v-if="item.category==3">
                                    <span class="floor-num">{{key+1}}楼   {{item.createTime}}</span>
                                    <el-card class="comment-item">
                                        <i class="el-icon-chat-dot-square replay-btn"
                                           @click="reply(key+1,item.author_id,item.content.substring(0,20),item.author_name,item.Id)"></i>
                                        <div class="flex comment-content align-center">
                                            <el-avatar :size="35" src="" shape="square"
                                                       style="margin-right: 10px;"></el-avatar>
                                            <span class="author">{{item.author_name}}</span>：
                                            <div class="reply-content" v-html="item.content">

                                            </div>
                                        </div>
                                    </el-card>
                                </div>
                                <div v-if="item.category==1">
                                    <span class="floor-num">{{key+1}}楼     {{item.createTime}}</span>
                                    <el-card class="comment-item">
                                        <div class="">
                                            <!--                                            replay(int floor_num,string uid,string short_msg)-->
                                            <i class="el-icon-chat-dot-square replay-btn"
                                               @click="reply(key+1,item.author_id,item.content.substring(0,20),item.author_name,item.Id)"></i>
                                            <div class="flex comment-content align-center">
                                                <el-avatar :size="35" src="" shape="square"
                                                           style="margin-right: 10px;"></el-avatar>
                                                <span class="author">{{item.author_name}}</span>：
                                                <div class="reply-content" v-html="item.content">

                                                </div>
                                            </div>
                                        </div>
                                        <div style="color: white;font-size: small;margin: 10px;padding: 10px;background: #cccccc;border-radius: 2px">
                                            回复{{item.floor}}楼 <span class="author">{{item.reply_name}}</span> ：<span
                                                v-html="item.target_content"></span>
                                        </div>
                                    </el-card>
                                </div>
                            </div>

                        </div>
                        <div class="block">
                            <el-pagination
                                    layout="prev, pager, next"
                                    :total="50">
                            </el-pagination>
                        </div>
                        <el-divider>回复</el-divider>
                        <div id="reply" style="min-height: 300px">
                            <!--                            回复框-->
                            <quill-editor ref="text" v-model="myComment" class="myQuillEditor"
                                          :options="editorOption"
                                          style="margin-bottom: 10px;min-height: 200px;"></quill-editor>
                            <el-button type="primary" @click="comment">提交</el-button>

                        </div>
                    </el-card>
                </el-col>
            </el-row>
            <el-dialog :visible.sync="dialogVisible"
                       width="60%">
                <el-card :body-style="{background:'#ccc',color:'white',margin:'10px'}" style="margin: 10px;">
                    回复{{replyData.floor}}楼<span class="author">{{replyData.username}}</span>：
                    <div style="display: inline-block;" v-html="replyData.msg"></div>
                </el-card>
                <quill-editor ref="text" v-model="myReply" class="myQuillEditor"
                              :options="editorOption"
                              style="margin-bottom: 10px;min-height: 200px;"></quill-editor>
                <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="send()">确 定</el-button>
              </span>
            </el-dialog>
        </el-main>
        <el-aside style="padding: 20px">
            <el-card
                    style="display: flex;flex-direction: column;justify-content: center;text-align: center;box-shadow: none;">
                <div>广告位</div>
                <div>广告位</div>
                <div>广告位</div>
            </el-card>
        </el-aside>
    </el-container>
</template>

<script>
    import quillEditor from 'vue-quill-editor'
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import '../../static/overwrite.css'
    import axios from "axios"

    export default {
        data() {
            return {
                commentData: {},
                content: '',
                title: "",
                topic_id: "",
                editorOption: {},
                myComment: "",
                myReply: "",
                dialogVisible: false,
                replyData: {
                    floor: 1,
                    uid: '1',
                    msg: 'xxxx',
                    username: "马云",
                    msg_id: ""
                }
            }
        },
        mounted() {
            this.topic_id = this.$route.params.id;
            axios.request({
                url: "/index/BBS/topic?id=" + this.topic_id,
                method: "get"
            }).then((response) => {
                if (response.data.status !== 0) {
                    return 0;
                }
                this.content = response.data.data.content;
                this.title = response.data.data.title;
            });
            this.getComment();
        },
        methods: {
            submit() {
                console.log(this.$refs.text.value)
            }
            , handleClose(done) {
                this.dialogVisible = false;
            },
            reply(floor_num, uid, msg, username, msg_id) {
                this.dialogVisible = true;
                this.replyData.uid = uid;
                this.replyData.floor = floor_num;
                this.replyData.msg = msg;
                this.replyData.username = username;
                this.replyData.msg_id = msg_id
            },
            send() {
                this.$axios.request({
                    url: "/index/BBS/reply"
                    , data: {
                        floor: this.replyData.floor,
                        recipient_id: this.replyData.uid,
                        content: this.myReply,
                        t_id: this.topic_id,
                        msg_id: this.replyData.msg_id
                    }
                    ,method:"post"
                }).then((response)=>{
                    this.$message("发送成功");
                })
            },
            comment() {
                this.$axios.request({
                    url: '/index/BBS/comment'
                    , data: {
                        id: this.topic_id,
                        content: this.myComment,
                    }
                    , method: "post"
                }).then((response) => {
                    if (response.data.status === 0) {
                        this.$message("评论成功");
                        this.myComment = "";
                    }
                })
            }
            , getComment() {
                this.$axios.request({
                    url: "/index/BBS/commentRender"
                    , data: {
                        id: this.topic_id
                    },
                    method: "post"
                }).then((response) => {
                    this.commentData = response.data.data;
                    console.log(this.commentData);
                })
            }
        },
        name: "topics",
        components: quillEditor
    }
</script>

<style scoped>
    .el-card {
        box-shadow: none;
    }

    .floor-num {
        color: gray;
        font-size: small;
    }

    .comment-item {
        margin: 10px;
        padding: 10px;
    }

    .replay-btn {
        color: cadetblue;
        float: right;
        display: block;
        cursor: pointer;
    }

    .author {
        margin: 0;
        color: cadetblue;
        font-weight: bold;
    }

    #title {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        color: dodgerblue;
        margin: 0.5rem;
        font-size: large;
        text-align: center;
    }

    .domain {
        min-height: 200px;
    }

</style>



