<template>
    <el-main>
        <el-row>
            <el-col>
                <el-card class="box-card">
                    <div class="bg-gradual-blue" style="display: flex;align-items: center;padding: 20px;">
                        <el-avatar :size="100">
                            SICAU
                        </el-avatar>
                        <p style="margin-left: 10px;font-weight: bolder"><span class="p-name">{{info.name}}</span>
                            <span>话题数 ：{{info.topic}}K</span>
                            <span>关注数：{{info.watch}}K</span>
                        </p>
                    </div>
                    <div style="margin-top: 30px;">
                        <el-tabs v-model="activeName" type="card">
                            <el-tab-pane label="推荐文章" name="n1">
                                <router-link style="text-decoration: none" :to="{name:'topics',params:{id:item.id}}"
                                             v-for="(item,key) in items.comment" :key="key">
                                    <div class="topic-item"><span>{{item.title}}</span></div>
                                </router-link>
                            </el-tab-pane>
                            <el-tab-pane label="精选文章" name="n2">
                                <router-link style="text-decoration: none" :to="{name:'topics',params:{id:item.id}}"
                                             v-for="(item,key) in items.comment" :key="key">
                                    <div class="topic-item"><span>{{item.title}}</span></div>
                                </router-link>
                            </el-tab-pane>
                            <el-tab-pane label="最新文章" name="n3">
                                <router-link style="text-decoration: none" :to="{name:'topics',params:{id:item.id}}"
                                             v-for="(item,key) in items.comment" :key="key">
                                    <div class="topic-item"><span>{{item.title}}</span></div>
                                </router-link>
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </el-main>
</template>

<script>
    export default {
        name: "category",
        data() {
            return {
                info: {
                    name: "四川农业大学",
                    watch: 20,
                    topic: 30,
                    id: 123
                },
                activeName: "n1",
                items: {
                    comment: {
                        title: "",
                        reply: "",
                        author: ""
                    }
                    , good: {}
                    , latest: {}
                }
            }
        },
        mounted() {
            this.info.id = this.$route.params.id;
            this.$axios.request({
                url: "/index/column/get?id=" + this.info.id
            }).then((response) => {
                this.info.name = response.data.data.group_name;
            });
            this.loadComment(1, 10);
        },
        methods: {
            loadComment(page, limit) {
                this.$axios.request({
                    url: "/index/topics/index?gid=" + this.info.id + "&page=" + page + "&limit=" + limit
                }).then((response) => {
                    this.items.comment = response.data.data;
                })
            }
        }

    }
</script>

<style scoped>
    .topic-item {
        padding: 10px;
        font-weight: bolder;
        border-radius: 4px;
        border: 1px #f0f0f0 solid;
        margin: 5px;
        color: #333333;
    }
</style>



