<template>
    <el-main>
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="block">
                    <el-carousel height="300">
                        <el-carousel-item class="bg-purple-dark" v-for="item in 4" :key="item">
                            <h3 class="small">{{ item }}</h3>
                        </el-carousel-item>
                    </el-carousel>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-card shadow="hover">
                    <div slot="header" class="clearfix">
                        大家都在逛
                    </div>
                    <div style="display: flex">
                        <router-link style="text-decoration: none" v-for="item in category"
                                     :to="{name: 'category',params:{id:item.id}}">
                            <div class="small-card bg-cyan">
                                <span>{{item.group_name}}</span>
                            </div>
                        </router-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </el-main>
</template>

<script>
    export default {
        name: "index",
        data() {
            return {
                msg: "123",
                count: 0,
                category: [
                    {
                        group_name: "四川农业大学"
                        , id: 123
                    }
                ]
            }
        },
        mounted() {
            this.$axios.request({
                url: "/index/column/show"
            }).then((response) => {
                this.category = response.data.data;
            })
        },
        methods: {
        }
    }
</script>

<style scoped>
    .el-row {
        margin-bottom: 20px;

    &
    :last-child {
        margin-bottom: 0;
    }

    }
    .el-col {
        border-radius: 4px;
    }

    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }

    .grid-content {
        border-radius: 4px;
        min-height: 36px;
    }

    .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
    }

    .small-card {
        min-width: 100px;
        max-width: 250px;
        padding: 5px;
        height: 50px;
        font-weight: bolder;
        font-size: 0.9rem;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px;
    }
</style>



